html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

:root {
  --body-text: hsla(234, 30%, 18%, 1);
  --header-text: hsla(0, 0%, 0%, 1);
  --btn-bg: hsla(0, 100%, 70%, 1);
  --btn-text: hsla(0, 0%, 100%, 1);
  --section2-bg: hsla(194, 57%, 95%, 1);
  --card-bg: hsla(0, 0%, 100%, 1);
  --view-all: hsla(191, 100%, 40%, 1);
  --year-bg: hsla(220, 60%, 20%, 1);
  --section-text: hsla(210, 14%, 58%, 1);
  --border-color: hsla(0, 0%, 88%, 1);
}

.container {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
}

body,
* {
  font-family: "Heebo", sans-serif;
  color: var(--body-text);
}

h1 {
  font-size: 2rem;
  font-weight: 700;
  line-height: 2.5rem;
}
@media (min-width: 23.43875em) {
  h1 {
    font-size: 2.75rem;
    line-height: 3.75rem;
  }
}

h2 {
  font-size: 1.375rem;
  font-weight: 700;
  line-height: 1.875rem;
}
@media (min-width: 23.43875em) {
  h2 {
    font-size: 1.625rem;
    line-height: 2.386875rem;
  }
}

h3 {
  font-size: 1.125rem;
  font-weight: 400;
  line-height: 3.75rem;
}
@media (min-width: 23.43875em) {
  h3 {
    font-size: 1.375rem;
  }
}

p {
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.46875rem;
}

.header {
  display: none;
}
@media (max-width: 23.43875em) {
  .header {
    display: flex;
    justify-content: end;
    width: 100%;
    height: 3.125rem;
  }
  .header__menu-button {
    margin: 1.25rem 0.75rem 0.75rem 0;
    background-color: transparent;
    border: none;
    width: 1.875rem;
    height: 1.125rem;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    cursor: pointer;
  }
  .header__menu-icon, .header__menu-icon::before, .header__menu-icon::after {
    background-color: var(--body-text);
    width: 1.875rem;
    height: 1.6px;
    position: absolute;
    display: block;
    transition: all 0.5s ease-in-out;
  }
  .header__menu-icon::before, .header__menu-icon::after {
    content: "";
  }
  .header__menu-icon::before {
    transform: translateY(-0.5625rem);
  }
  .header__menu-icon::after {
    transform: translateY(0.5625rem);
  }
}

@media (max-width: 23.43875em) {
  :is(header:hover, header:focus-within) .header__menu-icon {
    background-color: transparent;
    transform: rotate(720deg);
  }
  :is(header:hover, header:focus-within) .header__menu-icon::before {
    transform: rotate(45deg);
  }
  :is(header:hover, header:focus-within) .header__menu-icon::after {
    transform: rotate(-45deg);
  }
  :is(header:hover, header:focus-within) nav {
    display: block;
  }
}
.box {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-end;
  margin: 1.6875rem 3.75rem 8.75rem 0;
}

nav ul {
  list-style-type: none;
  display: flex;
  flex-flow: row nowrap;
  gap: 2.0625rem;
}
nav li {
  cursor: pointer;
}
nav a {
  display: block;
  text-align: center;
  width: 80%;
  margin: auto;
  font-size: 1.25rem;
  font-weight: 500;
  line-height: 1.83625rem;
}
nav a:any-link {
  text-decoration: none;
}
nav a:hover, nav a:focus {
  transform: scale(1.2);
  transition: all 0.3s;
}
@media (max-width: 23.43875em) {
  nav {
    background-color: var(--section-text);
    display: none;
    transform-origin: top center;
    animation: showMenu 0.5s ease-in-out forwards;
  }
  nav ul {
    flex-flow: column nowrap;
  }
  nav li {
    padding: 0.5rem;
    border-top: 0.0625rem solid var(--body-text);
  }
  nav a:any-link {
    color: var(--body-text);
  }
  @keyframes showMenu {
    0% {
      transform: scaleY(0);
    }
    80% {
      transform: scaleY(1.2);
    }
    100% {
      transform: scaleY(1);
    }
  }
}

.active {
  color: var(--btn-bg);
}

.profile {
  margin: 2.0625rem auto 0;
  text-align: center;
  margin: 0 2rem;
}
@media (min-width: 23.43875em) {
  .profile {
    display: flex;
    flex-flow: row-reverse nowrap;
    justify-content: space-between;
    text-align: start;
    margin: 0 9.25rem;
  }
}
.profile__pic {
  width: 10.89875rem;
  height: 11.25rem;
  margin: 0 0 2.125rem;
}
@media (min-width: 23.43875em) {
  .profile__pic {
    width: 15.1875rem;
    height: 15.1875rem;
  }
}
.profile__intro {
  margin: 0 2rem;
}
@media (min-width: 23.43875em) {
  .profile__intro--wrap {
    white-space: nowrap;
  }
}
.profile__about {
  margin: 1.3125rem auto 1.6875rem;
  line-height: 1.46875rem;
}
.profile__resume {
  width: 13rem;
  height: 2.9375rem;
  margin: 0 auto 3.6875rem;
  border-radius: 0.125rem;
  border: none;
  background-color: var(--btn-bg);
  color: var(--btn-text);
  font-size: 1.25rem;
  font-weight: 500;
  line-height: 1.83625rem;
}
.profile__resume:hover, .profile__resume:active, .profile__resume:focus-within {
  transform: scale(1.2);
  transition: 0.5s ease-in-out;
}

.posts {
  background-color: var(--section2-bg);
  padding: 0 0.625rem 2.375rem;
}
@media (min-width: 23.43875em) {
  .posts {
    padding: 0 9.25rem 2rem;
  }
}
.posts__title {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
@media (min-width: 23.43875em) {
  .posts__title {
    justify-content: space-between;
  }
}
.posts__title p {
  display: none;
}
@media (min-width: 23.43875em) {
  .posts__title p {
    display: block;
    color: var(--view-all);
    cursor: pointer;
  }
  .posts__title p:hover, .posts__title p:active {
    transform: scale(1.2);
    transition: 0.5s ease-in-out;
  }
}
.posts__grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: repeat(2, 1fr);
  gap: 1.0625rem;
  place-content: center;
}
@media (min-width: 23.43875em) {
  .posts__grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.25rem;
  }
}

.card {
  background-color: var(--card-bg);
  border-radius: 0.25rem;
  padding: 1.1875rem;
  box-shadow: 0 0.25rem 0.625rem 0 hsla(204, 86%, 86%, 0.25);
}
.card__duration {
  margin: 0.75rem 0 1.1875rem;
}
@media (min-width: 23.43875em) {
  .card__duration {
    font-size: 1.125rem;
    line-height: 1.6525rem;
  }
}
.card__divider {
  margin-left: 1.5rem;
  margin-right: 1.5rem;
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: repeat(3, 1fr);
  gap: 1.9375rem;
}

.featured-works__title {
  text-align: center;
}
@media (min-width: 23.43875em) {
  .featured-works__title {
    text-align: start;
    margin: 0.9375rem 9.25rem;
  }
}
.featured-works__card {
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: repeat(2, 1fr);
  margin: 0 1.125rem;
  border-bottom: 0.0625rem solid var(--border-color);
}
@media (min-width: 23.43875em) {
  .featured-works__card {
    margin: 0 9.25rem;
    display: flex;
    flex-flow: row nowrap;
    gap: 1.125rem;
  }
}
.featured-works__photo {
  width: 21.1875rem;
  height: 14.375rem;
}
@media (min-width: 23.43875em) {
  .featured-works__photo {
    width: 15.375rem;
    height: 11.875rem;
    padding-bottom: 1.5rem;
  }
}
.featured-works__summary {
  padding: 1.125rem 0;
}
@media (min-width: 23.43875em) {
  .featured-works__summary {
    padding: 0;
  }
}
.featured-works__summary:first-child {
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 1.875rem;
}
@media (min-width: 23.43875em) {
  .featured-works__summary:first-child {
    font-size: 1.875rem;
    line-height: 2.75375rem;
  }
}
.featured-works__mid {
  display: flex;
  gap: 1.625rem;
  margin: 1rem 0 1.5rem;
}
.featured-works__year {
  width: 3.875rem;
  height: 1.5625rem;
  display: grid;
  place-content: center;
  background-color: var(--year-bg);
  border-radius: 1rem;
}
.featured-works__year p {
  font-weight: 900;
  line-height: 3.75rem;
  color: var(--btn-text);
}
@media (min-width: 23.43875em) {
  .featured-works__year p {
    font-size: 1.125rem;
  }
}
.featured-works__description {
  color: var(--section-text);
}
@media (min-width: 23.43875em) {
  .featured-works {
    font-size: 1.25rem;
    line-height: 1.83625rem;
  }
}

.title {
  margin: 1.5rem 0 1.125rem 1.125rem;
  font-size: 1.875rem;
  line-height: 3.75rem;
}
@media (min-width: 23.43875em) {
  .title {
    margin: 6.625rem 0 3.4375rem 14.6875rem;
    font-size: 2.75rem;
  }
}
@media (min-width: 23.43875em) {
  .title--works {
    margin: 6.625rem 0 3.4375rem 9.25rem;
  }
}

.blog-grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: repeat(4, 1fr);
  gap: 1.125rem;
}
@media (min-width: 23.43875em) {
  .blog-grid {
    gap: 2rem;
  }
}

.blog {
  margin: 0 1.125rem;
  padding-bottom: 1.5rem;
  border-bottom: 0.0625rem solid var(--border-color);
}
@media (min-width: 23.43875em) {
  .blog {
    margin: 0 14.6875rem;
    padding-bottom: 1.8125rem;
  }
}
.blog__subtitle {
  font-size: 1.625rem;
  font-weight: 500;
  line-height: 2.386875rem;
}
@media (min-width: 23.43875em) {
  .blog__subtitle {
    font-size: 1.875rem;
    line-height: 2.75375rem;
  }
}
@media (min-width: 23.43875em) {
  .blog__duration {
    font-size: 1.25rem;
    line-height: 1.83625rem;
    margin: 1rem 0 0.9375rem 0;
  }
}
.blog__divider {
  margin-left: 0.875rem;
  margin-right: 0.9375rem;
}
@media (min-width: 23.43875em) {
  .blog__divider {
    margin-top: 1rem;
    margin-bottom: 0.9375rem;
  }
}
.blog__description {
  color: var(--section-text);
}

.footer {
  margin-top: 9.375rem;
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: repeat(2, auto);
  place-content: center;
  gap: 1.625rem;
}
.footer__logos {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  gap: 2.1875rem;
}
.footer__logos img {
  display: block;
  width: 1.875rem;
  height: 1.875rem;
}
.footer__copyright {
  margin-bottom: 3.375rem;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  font-size: 0.875rem;
  font-weight: 400;
  line-height: 1.285rem;
}